*,
*::before,
*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
}
h1, h2, h3, h4, h5, h6 {
    font-size: inherit;
}
a{
    color: inherit;
}
@font-face {
    font-family: '3d-unicode';
    src: url(./assets/fonts/3d_unicode.ttf);
}
@font-face {
    font-family: 'BPG Mikheil Stefane';
    src: url(./assets/fonts/BPG_Mikhail_Stephan.ttf);
}
@font-face {
    font-family: 'bpg-classic-medium';
    src: url(./assets/fonts/bpg-classic-medium-webfont.ttf);
}
/* variables */
:root{
    --header-footer-background: rgba(15, 10, 44, 0.9);
    --background: #022906;
    --poem-card-background: rgb(193 160 133 / 70%);
    --px10: 10px;
    --px20: 20px;
    --px30: 30px;
}
/* variables */

html, body{
    height: 100%;
}
#root {
    min-height: 100%;
}

body {
    background-color: var(--background);
}
body::-webkit-scrollbar {
    width: 12px;               /* width of the entire scrollbar */
  }
  
  body::-webkit-scrollbar-track {
    background: var(--header-footer-background);        /* color of the tracking area */
  }
  
  body::-webkit-scrollbar-thumb {
    background-color: var( --poem-card-background);    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    border: 3px solid var(--header-footer-background);  /* creates padding around scroll thumb */
  }
.App{
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.container{
    max-width: 1400px;
    margin: 0 auto;
    width: 95%;
}
/* display */
.df {
    display: flex;
}
.db{
    display: block;
}
.justify-between {
    justify-content: space-between;
}
.justify-around {
    justify-content: space-around;
}
.justify-evenly {
    justify-content: space-evenly;
}
.justify-center{
    justify-content: center;
}
.align-center {
    align-items: center;
}
.fl-wrap {
    flex-wrap: wrap;
}
.fldir-col{
    flex-direction: column;
}
/* colors */
.color-white{
    color: #fff;
}
/* TEXT */
.ta-center{
    text-align: center;
}
.ta-right {
    text-align: right;
}
.bold{
    font-weight: bold;
}
/* TEXT */
