
.home {
    margin: 30px 0;
}
.greeting{

}
.author-card {
    max-width: 200px;
  
}
.author-card img{
    width: 100%;
    display: block;
    border-radius: 20px;
    border: 2px solid white;
    box-shadow: rgba(255, 255, 255, 0.4) 0px 8px 24px;
}
.about-site {
    font-size: var(--px20);
    flex-basis: 400px;
}
.about-site h1 {

}
.about-site p {
    margin-top: var(--px10);
    line-height: var(--px20);
}
.author-name{
    margin-top: var(--px10);
    font-size: 15px;
}

.comments{
    margin-top: var(--px20);
}
.comments-card{
    /* position: relative; */
    width: 500px;
    padding: var(--px10);
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: var(--px10);

}
.comments-title {
    padding: var(--px10) 0;
    font-size: calc(var(--px20) + var(--px10));
    background-color: rgba(13, 85, 49, 0.5);
}
.comments-body {
    min-height: max-content;
    overflow-y: auto;
    height: 400px;

}

.comments-body p {
    margin: var(--px10);
    padding: var(--px10);
    font-size: var(--px20);
}
.comment-wrapper{
    border-top-left-radius: var(--px10);
    border-top-right-radius: var(--px10);
    border-bottom-left-radius: var(--px10);
    margin: var(--px10) 0;
}
.comment-wrapper img {
    display: block;
    align-self: flex-end;
    height: 32px;
    margin-bottom: var(--px10);
}
.comments-body .comment-wrapper:nth-child(2n + 2){
    background-color: rgba(13, 85, 49, 0.5);

}
.comments-body .comment-wrapper:nth-child(2n + 1){
    background-color: rgba(116, 18, 116, 0.5);

}
.comments-footer {
    /* position: absolute;
    bottom: 0;
    left: 0;
    right: 0; */
    margin-top: var(--px20);
    background-color: rgba(13, 85, 49, 0.5);
}
.comments-footer input {
    font-size: var(--px20);
    width: 100%;
    padding: var(--px10);
}
.comments-footer button {
    font-size: var(--px20);
    padding: var(--px10);

}
.send-button img {
    width: var(--px20);
}


@media (max-width: 769px) {
    .greeting {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about-site {
        margin-top: 40px;
        flex-basis: 400px;
    }
}